  background-color: var(--aside-bg-color);
  color: var(--aside-text-color);
  padding: 20px 40px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.aside-account {
  display: flex;
  gap: 10px;
  align-items: center;
}

.aside-account h2 {
  font-weight: bold;
}

.aside-account-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  width: var(--account-icon-wh-size);
  height: var(--account-icon-wh-size);
}

.todays-task-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: contain;
  width: var(--option-icon-wh-size);
  height: var(--option-icon-wh-size);
}

.week-tasks-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: contain;
  width: var(--option-icon-wh-size);
  height: var(--option-icon-wh-size);
}

.all-tasks-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-size: contain;
  width: var(--option-icon-wh-size);
  height: var(--option-icon-wh-size);
}

li {
  list-style-type: none;
  margin-bottom: 10px;
}

button {
  border: none;
  outline: none;
  padding: 0;
  pointer-events: auto;
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}

button:hover {
  color: #9d5900;
}

.btnActive {
  color: #9d5900;
}

.project-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tag-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-list-ul {
  padding: 0;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
  margin-top: 10px;
  max-width: 200px;
}

.tag-list-ul {
  padding: 0;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
  margin-top: 10px;
  max-width: 200px;
}
