___CSS_LOADER_EXPORT___.push([module.id, `/* Dialog backdrop styling */
::backdrop {
  background-color: rgba(0, 0, 0, 0.75); /* Darkened backdrop */
  opacity: 0.8;
}

/* Style for the dialog */
dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  border-radius: 15px;
  padding: 0;
}

#dialog-container {
  display: flex;
  gap: 10px;
  flex-direction: column;
  padding: 20px;
  height: 200px;
  width: 600px;
}

#dialog-form {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex: 0.9;
}

/* Input field styling */
input {
  padding: 10px;
  margin: 10px 0;
  width: 100%;
  font-size: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  outline: none;
  height: 100%;
}

#input-title {
  width: 100%;
}

/* Focus styles for inputs */
input:focus {
  border-color: var(--lib-head-bg-color);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

/* Custom add button */
#add-button {
  background-color: #f44336;
  border: none;
  color: white;
  padding: 10px 15px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  width: 100px;
  display: flex;
  justify-content: center;
}

#close-button {
  background: transparent;
  display: flex;
  justify-content: end;
}

#add-button:hover {
  background-color: #e53935;
}

.close-div,
#button-div {
  display: flex;
  justify-content: end;
}
